<template>
  <v-container fluid class="profile">
    <v-form ref="form" v-model="valid">
    <v-row wrap align="center">
      <!-- Avatar and Buttons -->
      <v-col cols="12" class="text-center" lg="12" md="12">
        <v-avatar size="100px" :color="isExistingImage ? '' : 'primary' ">
          <img
            v-if="this.isExistingImage == true"
            :src="!newUserImage.length ? userProfile.profile_picture : newUserImage[0]"
            alt="UserImage"
          />
          <img v-else 
            :src="!newUserImage.length ? defaultImage : newUserImage[0]"
            alt="UserImage" />
        </v-avatar>
        <p v-show="flagImageNotAllowed" class="mt-2 mb-0 profile__color_back_button" >La imagen seleccionada debe ser menor a 700 KB</p>
      </v-col>
      <v-col cols="12" class="text-center" lg="12" md="12">
        <input
          v-show="false"
          v-bind="form.inputImage.props"
          @change="addUserImage"
        />
        <v-btn class="ma-2" color="primary" dark @click.prevent="uploadImage">
          Cambiar Foto
          <v-icon dark right>mdi-pencil</v-icon>
        </v-btn>
        <v-spacer></v-spacer>
        <!--<v-spacer></v-spacer>
        <v-btn v-show="isExistingImage" class="ma-2" color="red" dark>
          Eliminar Foto
          <v-icon dark right>mdi-trash-can</v-icon>
        </v-btn>-->
      </v-col>

      <!-- Title -->
      <v-col cols="12" class="pl-9">
        <h1 class="profile__title">Datos Personales</h1>
      </v-col>
      <!-- Data -->
      <v-col cols="12">
        <v-card class="profile__card_data ml-6 mr-6">
          <v-list>
            <v-list-item>
              <v-list-item-content>
                <v-list-item-title><h3 class="profile__color_subtitles">Nombre</h3></v-list-item-title>
                <v-text-field 
                  v-model="form.name.value"
                  v-bind="form.name.props"
                  class="profile__text_field">
                </v-text-field>
              </v-list-item-content>
            </v-list-item>
            <v-list-item>
              <v-list-item-content>
                <v-list-item-title><h3 class="profile__color_subtitles">Correo Electrónico</h3></v-list-item-title>
                <v-text-field 
                  v-model="form.email.value"
                  v-bind="form.email.props"
                  class="profile__text_field">
                </v-text-field>
              </v-list-item-content>
            </v-list-item>
            <v-list-item>
              <v-list-item-content>
                <v-list-item-title><h3 class="profile__color_subtitles">Fecha de cumpleaños</h3></v-list-item-title>
                <v-menu
                  v-model="menu"
                  v-bind="form.menu.props"
                >
                  <template v-slot:activator="{ on }">
                    <v-text-field
                      v-model="birthdayDate"
                      v-on="on"
                      v-bind="form.birthday.props"
                    ></v-text-field>
                  </template>
                  <v-date-picker
                    v-model="birthdayDate"
                    v-bind="form.pickerDate.props"
                    @change="saveBirthdayDate"
                  ></v-date-picker>
                </v-menu>
              </v-list-item-content>
            </v-list-item>

            <v-list-item>
              <v-list-item-content>
                <v-list-item-title><h3 class="profile__color_subtitles">Género</h3></v-list-item-title>
                <v-select
                  v-model="form.gender.value"
                  v-bind="form.gender.props"
                ></v-select>
              </v-list-item-content>
            </v-list-item>

            <v-list-item>
              <v-list-item-content>
                <v-list-item-title><h3 class="profile__color_subtitles">Número Telefónico</h3></v-list-item-title>
                <v-text-field
                  v-model="form.phone.value"
                  v-bind="form.phone.props"
                ></v-text-field>
              </v-list-item-content>
            </v-list-item>
          </v-list>
        </v-card>
      </v-col>

      <!-- Button -->
      <v-col cols="12">
        <v-row justify="center" class="profile__card_data">
          <v-col cols="12" lg="4" md="4" sm="2" pt-3 class="categories-btn pr-8 pl-8">
            <v-btn block color="primary" :disabled="!valid" @click="userDataToUpdate" large>Guardar Cambios</v-btn>
          </v-col>
        </v-row>
      </v-col>
    </v-row>
    </v-form>
  </v-container>
</template>

<script>
import User from "@/models/user";
import DefaultUserImage from "@/assets/iconsa/ic_profile_picture_placeholder.png";
import { mapState, mapActions } from "vuex";
export default {
  data() {
    return {
      valid: true,
      form: User.getFormUserProfile(),
      defaultImage: DefaultUserImage,
      flagImageNotAllowed: false,
      isExistingImage: false,
      newUserImage: [],
      birthdayDate: null,
      menu: false,
      updateDataUser: {}
    };
  },
  async mounted() {
      await this.getUserProfile();
      this.form.pickerDate.props.max = new Date().toISOString().substr(0, 10);
      this.isExistingImage = (this.userProfile.profile_picture != null) ? true : false;
      this.form.name.value = (this.userProfile.name != null) ? this.userProfile.name : this.data.name;
      this.form.email.value = (this.userProfile.email != null) ? this.userProfile.email : this.data.email;
      this.birthdayDate = (this.userProfile.birthday != null) ? this.userProfile.birthday : "";
      this.form.gender.value = (this.userProfile.gender != null) ? this.userProfile.gender : "";
      this.form.phone.value = (this.userProfile.phone_number != null) ? this.userProfile.phone_number.substr(2) : this.data.phone_number.substr(2);
  },
  computed: {
    ...mapState("profile", ["userProfile"]),
    ...mapState("login", ["data"])
  },
  methods: {
    ...mapActions("profile", ["getUserProfile", "updateUserProfile"]),
    saveBirthdayDate(birthdayDate) {
      this.$refs.menu.save(birthdayDate);
    },
    uploadImage() {
      this.$refs.upload.click();
      //console.log("newUserImage", this.newUserImage);
    },
    addUserImage() {
      const image = this.$refs.upload.files;
      if (image && image[0]) {
        this.newUserImage = [] //remove data because user chose a new image
        if (image[0].size <= 700000){
          this.flagImageNotAllowed = false;
          const reader = new FileReader();
          reader.onload = ({ target }) => this.newUserImage.push(target.result);
          reader.readAsDataURL(image[0]);
        }else{
          this.flagImageNotAllowed = true;
        }
      }
    },
    async userDataToUpdate(){
      if (this.$refs.form.validate()){
        this.updateDataUser.name = this.form.name.value;
        this.updateDataUser.email = this.form.email.value;
        this.updateDataUser.birthday = this.birthdayDate;
        this.updateDataUser.gender = this.form.gender.value;
        if (this.userProfile.profile_picture != null && this.newUserImage[0] == null){
          this.updateDataUser.profile_picture = this.userProfile.profile_picture;
        }else{
          this.updateDataUser.profile_picture = (this.newUserImage[0] != null) ? this.newUserImage[0] : null ;
        }
        await this.updateUserProfile(this.updateDataUser);
      }
    }
  },
  watch: {
    menu(val) {
      val && setTimeout(() => (this.$refs.picker.activePicker = "YEAR"));
    }
  }
};
</script>

